import type { CacheEntry, CacheProvider } from './interface';

export class IndexedDBCache implements CacheProvider {
    private dbPromise: Promise<IDBDatabase> | null = null;
    private maxEntries: number;
    private readonly dbName: string;
    private readonly dbVersion: number;

    constructor(maxEntries = 1000, dbName = 'StencilCacheDB', dbVersion = 1) {
        this.maxEntries = maxEntries;
        this.dbName = dbName;
        this.dbVersion = dbVersion;
    }


    private openDB(): Promise<IDBDatabase> {
        if (!this.dbPromise) {
            this.dbPromise = new Promise((resolve, reject) => {
                const request = window.indexedDB.open(this.dbName, this.dbVersion);

                request.onupgradeneeded = (event) => {
                    const db = (event.target as IDBOpenDBRequest).result;
                    if (!db.objectStoreNames.contains('cache')) {
                        const store = db.createObjectStore('cache', { keyPath: 'key' });
                        store.createIndex('timestamp', 'timestamp', { unique: false });
                    }
                };

                request.onsuccess = (event) => {
                    resolve((event.target as IDBOpenDBRequest).result);
                };

                request.onerror = () => {
                    this.dbPromise = null;
                    reject(request.error);
                };
            });
        }

        return this.dbPromise;
    }

    private async withTransaction<T>(
        mode: IDBTransactionMode,
        operation: (store: IDBObjectStore) => Promise<T>
    ): Promise<T> {
        const db = await this.openDB();
        const tx = db.transaction('cache', mode);
        return await operation(tx.objectStore('cache'));
    }

    async get(key: string): Promise<CacheEntry | null> {
        return await this.withTransaction('readonly', (store) => {
            return new Promise<CacheEntry | null>((resolve, reject) => {
                const req = store.get(key);
                req.onsuccess = () => {
                    const record = req.result;
                    resolve(record ? {
                        value: record.value,
                        timestamp: record.timestamp,
                        ttlMillis: record.expiredMillis
                    } : null);
                };
                req.onerror = () => reject(req.error);
            });
        });
    }

    async set(key: string, entry: CacheEntry): Promise<void> {
        // 優化：在同一個交易中檢查大小並設定值
        await this.withTransaction('readwrite', async (store) => {
            return new Promise<void>((resolve, reject) => {
                const countReq = store.count();
                countReq.onsuccess = () => {
                    const currentSize = countReq.result;
                    
                    const processSet = () => {
                        const req = store.put({ key, ...entry });
                        req.onsuccess = () => resolve();
                        req.onerror = () => reject(req.error);
                    };
                    
                    if (currentSize >= this.maxEntries) {
                        // 在同一交易中刪除最舊的條目
                        const index = store.index('timestamp');
                        const deleteReq = index.openCursor();
                        
                        deleteReq.onsuccess = (event) => {
                            const cursor = (event.target as IDBRequest).result;
                            if (cursor) {
                                const delReq = cursor.delete();
                                delReq.onsuccess = processSet;
                                delReq.onerror = () => reject(delReq.error);
                            } else {
                                processSet();
                            }
                        };
                        deleteReq.onerror = () => reject(deleteReq.error);
                    } else {
                        processSet();
                    }
                };
                countReq.onerror = () => reject(countReq.error);
            });
        });
    }

    async delete(key: string): Promise<void> {
        await this.withTransaction('readwrite', (store) => {
            return new Promise<void>((resolve, reject) => {
                const req = store.delete(key);
                req.onsuccess = () => resolve();
                req.onerror = () => reject(req.error);
            });
        });
    }

    async clear(): Promise<void> {
        await this.withTransaction('readwrite', (store) => {
            return new Promise<void>((resolve, reject) => {
                const req = store.clear();
                req.onsuccess = () => resolve();
                req.onerror = () => reject(req.error);
            });
        });
    }

    get defaultKeyPrefix(): string {
        return 'idb';
    }

    async size(): Promise<number> {
        return await this.withTransaction('readonly', (store) => {
            return new Promise<number>((resolve, reject) => {
                const req = store.count();
                req.onsuccess = () => resolve(req.result);
                req.onerror = () => reject(req.error);
            });
        });
    }


    setMaxEntries(maxEntries: number): void {
        this.maxEntries = maxEntries;
    }

    getMaxEntries(): number {
        return this.maxEntries;
    }
}