import { Component, Event, EventEmitter, h, Host, Prop, Watch } from '@stencil/core';

@Component({
  tag: 'storm-checkbox-group',
  styleUrl: 'storm-checkbox-group.css',
  shadow: false,
})
export class StormCheckboxGroup {
  private _dataSet: ICheckboxItem[];

  @Prop() type: 'checkbox' | 'radio' = 'checkbox';
  @Prop() labelClasses: Record<string, boolean> | string = 'custom-control-label';
  @Prop() isSwitch: boolean = false;
  @Prop() isInline: boolean = false;
  @Prop() dataSet: ICheckboxItem[] | string;
  @Prop({ mutable: true }) value: string | string[];
  @Prop({ mutable: true }) disabled?: boolean;

  @Event() valueChanged: EventEmitter<string | string[] | null>;

  @Watch('dataSet')
  dataSetWatcher(newValue: ICheckboxItem[] | string) {
    try {
      this._dataSet = typeof newValue === 'string' ? JSON.parse(newValue) : newValue;
    } catch (error) {
      console.error('Invalid JSON format:', error);
      return;
    }
    if (this.value) {
      this.valueWatcher(this.value);
    } else this.value = this.type === 'checkbox' ? this._dataSet.find(x => x.checked)?.id : this._dataSet.filter(x => x.checked).map(x => x.id);
    if (this.type === 'radio') {
      const checkedItems = this._dataSet.filter(x => x.checked);
      if (checkedItems.length === 0) {
        this._dataSet[0].checked = true;
      } else if (checkedItems.length > 1) {
        checkedItems
          .reverse()
          .slice(1)
          .forEach(item => (item.checked = false));
      }
    }
  }

  @Watch('value')
  valueWatcher(newValue: string | string[]) {
    if (Array.isArray(newValue)) this._dataSet.forEach(x => (x.checked = newValue.includes(x.id)));
    else this._dataSet.forEach(x => (x.checked = x.id == newValue));
  }

  check(item: ICheckboxItem) {
    if (this.type === 'radio') {
      this.uncheckAll();
    }
    item.checked = true;
    if (this.type === 'radio') {
      this.value = item.id;
    } else if (Array.isArray(this.value)) {
      if (!this.value.includes(item.id)) this.value = [...this.value, item.id];
    } else {
      this.value = [item.id];
    }
  }

  uncheck(item: ICheckboxItem) {
    item.checked = false;
    if (this.type === 'radio') {
      if (this.value === item.id) {
        this.value = null;
      }
    } else if (Array.isArray(this.value)) {
      this.value = this.value.filter(id => id !== item.id);
    }
  }

  uncheckAll() {
    if (this._dataSet) {
      this._dataSet.filter(x => x.checked).forEach(x => (x.checked = false));
    }
  }

  componentWillLoad() {
    if (this.dataSet) {
      this.dataSetWatcher(this.dataSet);
    }
  }

  dataSetRender() {
    return this._dataSet.map(item => {
      if (!item.id) item.id = item.label;
      return (
        <storm-checkbox
          type={this.type}
          label={item.label}
          labelClasses={item.labelClasses ?? this.labelClasses}
          disabled={this.disabled || item.disabled}
          checked={item.checked}
          isSwitch={this.isSwitch}
          isInline={this.isInline}
          onValueChanged={ev => {
            if (ev.detail) {
              this.check(item);
            } else {
              this.uncheck(item);
            }
          }}
        ></storm-checkbox>
      );
    });
  }

  render() {
    return <Host>{this.dataSetRender()}</Host>;
  }
}

export interface ICheckboxItem {
  id?: string;
  label: string;
  labelClasses?: string;
  checked?: boolean;
  disabled?: boolean;
}
