import { Component, Event, EventEmitter, h, Prop } from '@stencil/core';
import type { ListItemInterface } from '../../utils/interface';
import { createFunctionFromString, isModuleFunctionString, parseClassNames } from '../../utils/utils';
import type { ToolbarItemInterface } from './interface';

@Component({
  tag: 'storm-toolbar',
  styleUrl: 'storm-toolbar.css',
  shadow: false,
})
export class StormToolbar {
  @Prop() dataSet: ToolbarItemInterface[];
  @Prop({ mutable: true }) timeStamp?: number;
  @Prop({ mutable: true }) class: Record<string, boolean> | string;

  @Event() toolbarAction: EventEmitter<string>;

  renderToolbarItem(item: ToolbarItemInterface) {
    if (typeof item.visible === 'function' && !item.visible()) return;
    if (typeof item.visible === 'boolean' && !item.visible) return;
    if (item.type === 'group') {
      return this.renderDropdown(item);
    } else if (item.type === 'divider') {
      return (
        <div class="d-inline-flex align-middle mx-2">
          <div class="vr"></div>
        </div>
      );
    } else {
      return (
        <storm-toolbar-item
          dataSet={item}
          onToolbarAction={e => {
            this.handleAction(e, item);
          }}
        ></storm-toolbar-item>
      );
    }
  }

  handleAction(_: Event, item: ToolbarItemInterface, dropdownItem: ListItemInterface = null) {
    if (!item.click) return;
    if (typeof item.click === 'string') {
      if (!isModuleFunctionString(item.click)) {
        throw new Error('Invalid function string');
      }
      item.click = createFunctionFromString(item.click);
    }
    item.click(item, dropdownItem);
    this.toolbarAction.emit(dropdownItem ? dropdownItem.id : item.id);
  }

  renderDropdown(item: ToolbarItemInterface) {
    if (!item || !item.items || item.items.length === 0) return;
    return (
      <div class="d-inline-flex align-middle">
        <storm-dropdown
          dataSet={item.items}
          trigger="click"
          toggleIcon="more_vert"
          type="none"
          selection="none"
          nullable={true}
          showSelected={false}
          onItemClick={e => {
            this.handleAction(e, item, e.detail);
          }}
        ></storm-dropdown>
      </div>
    );
  }

  render() {
    if (!this.dataSet) return;
    return (
      <div class="w-100">
        <div class={{ 'd-flex': true, ...parseClassNames(this.class) }}>
          {this.dataSet.map(x => {
            return this.renderToolbarItem(x);
          })}
        </div>
      </div>
    );
  }
}
