:host {
  display: block;
}

:host storm-button,
:host storm-dropdown,
:host div {
  vertical-align: middle;
}


:host .btn:hover:not(.active) {
  color: white;
}

:host .btn.btn-outline-primary:hover {
  background-color: #e91e63;
}

:host .btn.disabled {
  color: #7b809a;
  border-color: #7b809a;
  cursor: not-allowed;
}