:host {
    display: block;
    background-color: #fff;
}

:host .list-group-item {
    border: none !important;
    white-space: nowrap !important;
    cursor: pointer !important;
    margin-bottom: 0 !important;
}

:host .close.material-icons,
:host .open.material-icons:hover {
    opacity: 0.5;
}

:host .open.material-icons,
:host .close.material-icons:hover {
    opacity: 1;
}

:host .material-icons:empty {
    width: 1.25rem;
}

:host .indent {
    margin-left: 0.575rem;
    margin-right: 0.575rem;
}

:host .leaf {
    margin-left: 0.6rem;
}