
import { CacheService } from '../cache/cache-service';
import { IndexedDBCache } from '../cache/indexed-db-cache';
import type { FetchOption } from './interface';

const cacheService = new CacheService(new IndexedDBCache(), {
    regionName: 'fetchDataSource',
});

export async function fetchDataSource(options: FetchOption): Promise<any> {
    // 1. 合併預設值
    options = {
        method: 'POST',
        contentType: 'application/json',
        ...options,
    };

    // 2. 以 JSON.stringify(options) 當作單筆快取的 key
    const key = JSON.stringify(options);

    // 3. 嘗試從快取讀取 (使用預設 region)
    const cached = await cacheService.get<any>(key);
    if (cached !== null) {
        return cached;
    }

    // 4. 快取不存在或已過期，真正執行 fetch
    const response = await fetch(options.url, {
        method: options.method,
        body: options.method !== 'GET' ? JSON.stringify(options.body ?? {}) : null,
        headers: new Headers({
            'Content-Type': options.contentType,
        }),
    });

    if (!response.ok) {
        throw new Error(`Network response was not ok: ${response.statusText}`);
    }

    const data =
        options.contentType === 'application/json'
            ? await response.json()
            : await response.text();

    // 5. 把新資料寫回快取 (使用預設 region)
    await cacheService.set(key, data);

    return data;
}
