import { Component, Element, Event, EventEmitter, h, Host, Prop, Watch } from '@stencil/core';
import type { FetchOption } from '../../utils/fetch-data-source/interface';

@Component({
  tag: 'storm-dropdown-tree',
  styleUrl: 'storm-dropdown-tree.css',
  shadow: false,
})
export class StormDropdownTree {
  private _dropdownRef: HTMLStormDropdownElement;
  private _treeRef: HTMLStormTreeViewElement;
  private _text: string;

  @Element() host: HTMLElement;

  @Prop({ mutable: true }) label: string;
  @Prop({ mutable: true }) heading: string;
  @Prop({ mutable: true }) text?: string;
  @Prop({ mutable: true }) value?: string | string[];
  @Prop({ mutable: true }) dataSet?: object | object[];
  @Prop({ mutable: true }) dataSource?: FetchOption | Function | string;
  @Prop({ mutable: true }) disabled?: boolean;
  @Prop() nullable: boolean = false;
  @Prop() fieldId: string = 'id';
  @Prop() fieldLabel: string = 'label';
  @Prop() fieldChildren: string = 'children';
  @Prop() expandLevel: number = 2;
  @Prop() nodeSelectableRule?: string | Function;
  @Prop() size: 'Default' | 'Small' = 'Default';
  @Prop() partialLoad: boolean = false;
  @Prop() loading: 'eager' | 'lazy' = 'lazy';
  @Prop() class: Record<string, boolean> | string;

  @Prop({ mutable: true }) valid?: boolean;
  @Prop() required?: boolean;

  @Prop({ mutable: true }) searchText?: string;
  @Prop() searchable: boolean = false;
  @Prop() selection: 'single' | 'multiple' | 'none' = 'single';

  @Prop({ mutable: true }) timeStamp?: number;

  @Event() valueChanged: EventEmitter<string | string[]>;
  @Event() textChanged: EventEmitter<string>;
  @Event() searchChanged: EventEmitter<string>;

  @Watch('value')
  valueWatcher() {
    this.updateValue();
    this.valueChanged.emit(this.value);
  }

  @Watch('text')
  textWatcher() {
    this.textChanged.emit(this.text);
  }

  @Watch('dataSet')
  dataSetWatcher() {
    this._treeRef.dataSet = this.dataSet;
  }

  updateValue() {
    if (this._treeRef.value != this.value) this._treeRef.value = this.value;
    if (this._dropdownRef.value != this.value) this._dropdownRef.value = this.value;
  }

  componentDidLoad() {
    this.updateValue();
  }

  renderTree() {
    return (
      <storm-tree-view
        ref={elm => (this._treeRef = elm)}
        partialLoad={this.partialLoad}
        loading={this.loading}
        value={this.value}
        class={this.class}
        dataSet={this.dataSet}
        dataSource={this.dataSource}
        fieldId={this.fieldId}
        fieldLabel={this.fieldLabel}
        fieldChildren={this.fieldChildren}
        nullable={this.nullable}
        selection={this.selection}
        expandLevel={this.expandLevel}
        nodeSelectableRule={this.nodeSelectableRule}
        searchable={this.searchable}
        onValueChanged={ev => {
          ev.stopPropagation();
          this.value = ev.detail;
        }}
        onNodeClick={async ev => {
          ev.stopPropagation();
          await this._dropdownRef.hide();
        }}
        onSearchChanged={ev => {
          ev.stopPropagation();
          this.searchChanged.emit(ev.detail);
        }}
      ></storm-tree-view>
    );
  }

  async componentWillRender() {
    this._text = this.text;
    if (this._text == null) {
      if (this._treeRef && this.value) {
        const values = Array.isArray(this.value) ? this.value : [this.value];
        const texts = [];
        for (const value of values) {
          const node = await this._treeRef.getNodeById(value);
          if (node) {
            texts.push(await this._treeRef.getLabel(node));
          }
        }
        this._text = texts.join('、');
      } else {
        this._text = undefined;
      }
    }
  }

  render() {
    return (
      <Host>
        <storm-dropdown
          ref={elm => (this._dropdownRef = elm)}
          nullable={this.nullable}
          heading={this.heading}
          label={this.label}
          text={this._text}
          value={this.value}
          showSelected={true}
          valid={this.valid}
          required={this.required}
          disabled={this.disabled}
          timeStamp={this.timeStamp}
          type="select"
          onValueChanged={ev => {
            ev.stopPropagation();
            this.value = ev.detail;
            if (this.value == null) {
              this.text = undefined;
            }
          }}
          onUnselectedAll={async ev => {
            ev.stopPropagation();
            await this._treeRef.unselectAll();
          }}
          onShowMenu={async ev => {
            ev.stopPropagation();
            await this._treeRef.loadDataSet();
            if (this.searchable && this._treeRef) {
              requestAnimationFrame(async () => {
                await this._treeRef.setSearchFocus();
              });
            }
          }}
        >
          {this.renderTree()}
        </storm-dropdown>
      </Host>
    );
  }
}
