import { Component, Element, Event, EventEmitter, Host, Prop, h } from '@stencil/core';
import type { ConditionInterface, ListItemInterface } from '../../../utils/interface';
import { calculateObjectValue, evaluateConditions, getFieldValue, isConditions } from '../../../utils/utils';
import type { ToolbarItemInterface } from '../../storm-toolbar/interface';
import type { TableColumnInterface } from '../interface';
import type { StormTable } from '../storm-table';
import type { ClickActionDetail } from '../storm-table-toolbar/interface';

@Component({
  tag: 'storm-table-toolbar',
  styleUrl: 'storm-table-toolbar.css',
  shadow: false,
})
export class StormTableToolbar {
  private _dateSet: ToolbarItemInterface[];

  @Element() host: HTMLElement;

  @Prop() table: StormTable;
  @Prop() column: TableColumnInterface;
  @Prop() row: object;
  @Prop() isDynamicLabel: boolean = true;
  @Prop({ mutable: true }) timeStamp?: number;

  @Event() clickAction: EventEmitter<ClickActionDetail>;

  componentWillRender() {
    this._dateSet = this.column.buttons?.map(x => {
      const item: ToolbarItemInterface = {
        id: x.id,
        type: x.type,
        label: x.label,
        tooltip: x.tooltip,
        icon: x.icon,
        size: 'sm',
        buttonStyle: 'outline-primary',
        rounded: true,
        visible: () => {
          if (isConditions(x.visible)) {
            return evaluateConditions(x.visible as ConditionInterface[], this.row);
          }
          return calculateObjectValue(this, x.visible, this.row, this.table, getFieldValue(this.row, this.table.fieldIndex)) ?? true;
        },
        disabled: () => {
          if (isConditions(x.disabled)) {
            return evaluateConditions(x.disabled as ConditionInterface[], this.row);
          }
          return calculateObjectValue(this, x.disabled, this.row, this.table, getFieldValue(this.row, this.table.fieldIndex)) ?? false;
        },
        items: x.items
          ? x.items.map(
              y =>
                ({
                  id: y.id,
                  title: y.title,
                  icon: y.icon,
                  selected: y.selected,
                  visible: () => {
                    if (isConditions(y.visible)) {
                      return evaluateConditions(y.visible as ConditionInterface[], this.row);
                    }
                    return calculateObjectValue(this, y.visible, this.row, this.table, getFieldValue(this.row, this.table.fieldIndex)) ?? true;
                  },
                  disabled: () => {
                    if (isConditions(y.disabled)) {
                      return evaluateConditions(y.disabled as ConditionInterface[], this.row);
                    }
                    return calculateObjectValue(this, y.disabled, this.row, this.table, getFieldValue(this.row, this.table.fieldIndex)) ?? false;
                  },
                } as ListItemInterface),
            )
          : null,
        click: (_: ToolbarItemInterface, dropdownItem: ListItemInterface = null) => {
          if (x.action) x.action(this.row, x, this.table, dropdownItem);
          this.clickAction.emit({
            id: getFieldValue(this.row, this.table.fieldId) as string,
            field: this.column.field,
            actionId: x.id,
            dropdownItemId: dropdownItem ? dropdownItem.id : null,
          });
        },
      } as ToolbarItemInterface;
      return item;
    });
  }

  render() {
    return (
      <Host>
        <storm-toolbar
          class={{
            'storm-dynamic-label': this.isDynamicLabel,
            'justify-content-start': this.column.align === 'left',
            'justify-content-center': this.column.align === 'center',
            'justify-content-end': this.column.align === 'right',
          }}
          dataSet={this._dateSet}
          timeStamp={this.timeStamp}
        ></storm-toolbar>
      </Host>
    );
  }
}
