import { Component, Element, Event, EventEmitter, forceUpdate, h, Host, Listen, Method, Prop, Watch } from '@stencil/core';
import { fetchDataSource } from '../../utils/fetch-data-source/fetch-data-source';
import type { FetchOption } from '../../utils/fetch-data-source/interface';
import { FnSearchInput } from '../../utils/functional/functional';
import { SharedStyles } from '../../utils/shared-styles';
import { appendHighlight, calculateObjectValue, createFunctionFromString, debounce, getFieldValue, isModuleFunctionString, setFieldValue } from '../../utils/utils';
import type { StormTreeNode } from './storm-tree-node/storm-tree-node';

@Component({
  tag: 'storm-tree-view',
  styleUrl: 'storm-tree-view.css',
  shadow: true,
})
export class StormTreeView {
  private _searchText?: string;
  private _dataSet?: object[];
  private _items?: object[];
  private _dataSource?: FetchOption | Function;
  private _searchRef: HTMLStormInputGroupElement;
  private _nodeSelectableRule?: Function;
  private _nodeCheckRule?: Function;

  @Element() host: HTMLElement;

  @Prop() partialLoad: boolean = false;
  @Prop() loading: 'eager' | 'lazy' = 'eager';
  @Prop({ mutable: true }) value?: string | string[];
  @Prop({ mutable: true }) checked: string[] = [];

  @Prop() fieldId: string = 'id';
  @Prop() fieldLabel: string = 'label';
  @Prop() fieldChildren: string = 'children';

  @Prop() fieldSubId: string = 'subId';
  @Prop() fieldSubLabel: string = 'subLabel';
  @Prop() fieldSubChildren: string = 'subChildren';

  @Prop() fieldSelected: string = '_selected';
  @Prop() fieldChecked: string = '_checked';
  @Prop() fieldExpanded: string = '_expanded';
  @Prop() fieldDisabled: string = '_disabled';
  @Prop() fieldParent: string = '_parent';
  @Prop() fieldSelectable: string = '_selectable';
  @Prop() fieldCheckSelectable: string = '_checkSelectable';
  @Prop() fieldIcon: string = '_icon';
  @Prop() fieldHref: string = '_href';
  @Prop() fieldLevel: string = '_level';
  @Prop() fieldChildLoaded: string = '_loaded';
  @Prop() fieldElement: string = '_element';

  @Prop({ mutable: true }) expandIconAlign: 'left' | 'right' = 'left';
  @Prop({ mutable: true }) expandIcon?: string;
  @Prop({ mutable: true }) collapseIcon?: string;
  @Prop() color?: string;

  @Prop({ mutable: true }) dataSet?: object | object[];
  @Prop({ mutable: true }) dataSource?: FetchOption | Function | string;
  @Prop({ mutable: true }) fetch?: Function;
  @Prop() nullable: boolean = true;
  @Prop({ mutable: true }) expandLevel: number = 2;
  @Prop() structure: 'tree' | 'array' | 'auto' = 'auto';
  @Prop() searchable: boolean = false;
  @Prop() selection: 'single' | 'multiple' | 'none' = 'single';
  @Prop({ mutable: true }) nodeSelectableRule?: string | Function;
  @Prop() checkSelectable: boolean = false;
  @Prop({ mutable: true }) nodeCheckRule?: string | Function;
  @Prop({ mutable: true }) selectToCheck: boolean = true;

  @Event() dateSetLoaded: EventEmitter;
  @Event() valueChanged: EventEmitter<string | string[]>;
  @Event() nodeSelect: EventEmitter<string>;
  @Event() nodeUnselect: EventEmitter<string>;
  @Event() nodeClick: EventEmitter<string>;
  @Event() nodeDblClick: EventEmitter<string>;
  @Event() nodeCheck: EventEmitter<string>;
  @Event() nodeUncheck: EventEmitter<string>;
  @Event() checkedChanged: EventEmitter<string[]>;

  @Event() searchChanged: EventEmitter<string>;
  @Event() keydown: EventEmitter<KeyboardEvent>;

  @Listen('searchChanged')
  async searchChangedHandler(ev: CustomEvent<string>) {
    this._searchText = ev.detail;
    if (this.partialLoad) {
      this.cleanNodes();
      await this.loadDataSet();
    } else {
      this.rerender();
    }
  }

  @Listen('nodeSelect')
  nodeSelectHandler(ev: CustomEvent<string>) {
    switch (this.selection) {
      case 'single':
        this.value = ev.detail;
        break;
      case 'multiple': {
        const currentValue = Array.isArray(this.value) ? this.value : this.value ? [this.value] : [];
        if (!currentValue.includes(ev.detail)) this.value = [...currentValue, ev.detail];
        break;
      }
      default:
        console.warn(`Unknown selection mode: ${this.selection}`);
    }
  }

  @Listen('nodeUnselect')
  nodeUnselectHandler(ev: CustomEvent<string>) {
    switch (this.selection) {
      case 'single':
        if (this.value === ev.detail) {
          this.value = null;
        }
        break;
      case 'multiple': {
        const currentValue = Array.isArray(this.value) ? this.value : [];
        if (currentValue.includes(ev.detail)) this.value = currentValue.filter(id => id !== ev.detail);
        break;
      }
      default:
        console.warn(`Unknown selection mode: ${this.selection}`);
    }
  }

  @Listen('nodeCheck')
  nodeCheckHandler(ev: CustomEvent<string>) {
    if (this.selectToCheck) return;
    if (!this.checked.includes(ev.detail)) this.checked = [...this.checked, ev.detail];
  }

  @Listen('nodeUncheck')
  nodeUncheckHandler(ev: CustomEvent<string>) {
    if (this.selectToCheck) return;
    if (this.checked.includes(ev.detail)) this.checked = this.checked.filter(id => id !== ev.detail);
  }

  @Listen('valueChanged')
  async valueChangedHandler(): Promise<void> {
    if (!Array.isArray(this._items)) return;

    const valueArray = Array.isArray(this.value) ? this.value : this.value ? [this.value] : [];

    await Promise.all(
      this._items.map(async item => {
        const id = String(getFieldValue(item, this.fieldId) as string);
        const shouldSelect = valueArray.includes(id);
        const selected = (getFieldValue(item, this.fieldSelected) as boolean) === true;

        const promises: Promise<void>[] = [];
        if (shouldSelect) {
          promises.push(this.setNodeExpanded(item));
        }

        if (selected !== shouldSelect) {
          promises.push(this.setNodeSelectionState(item, shouldSelect));
        }

        return Promise.all(promises);
      }),
    );

    if (this.checkSelectable && this.selectToCheck) {
      this.checked = valueArray;
    }
  }

  @Listen('checkedChanged')
  async checkedChangedHandler(): Promise<void> {
    if (!Array.isArray(this._items)) return;

    await Promise.all(
      this._items.map(item => {
        const id = String(getFieldValue(item, this.fieldId) as string);
        const checkedArray = Array.isArray(this.checked) ? this.checked : this.checked ? [this.checked] : [];
        const shouldChecked = checkedArray.includes(id);
        const checked = (getFieldValue(item, this.fieldChecked) as boolean) === true;

        if (checked !== shouldChecked) {
          return this.setNodeCheckedState(item, shouldChecked);
        }
        return Promise.resolve();
      }),
    );
  }

  @Watch('value')
  async watchValueHandler() {
    this.valueChanged.emit(this.value);
  }

  @Watch('checked')
  async watchCheckedHandler() {
    this.checkedChanged.emit(this.checked);
  }

  async setNodeSelectionState(item: object, shouldSelect: boolean): Promise<void> {
    const node = await this.getTreeNode(item);

    if (node) {
      await (shouldSelect ? node.select() : node.unselect());
    } else {
      setFieldValue(item, this.fieldSelected, shouldSelect);
    }
    if (this.checkSelectable && this.selectToCheck) {
      await this.setNodeCheckedState(item, shouldSelect);
    }
  }

  async setNodeCheckedState(item: object, shouldChecked: boolean): Promise<void> {
    const node = await this.getTreeNode(item);

    if (node) {
      await (shouldChecked ? node.check() : node.uncheck());
    } else {
      setFieldValue(item, this.fieldChecked, shouldChecked);
    }
  }

  @Watch('dataSource')
  watchDataSourceHandler() {
    this.cleanNodes();
    if (typeof this.dataSource === 'string') {
      if (isModuleFunctionString(this.dataSource)) {
        this._dataSource = createFunctionFromString(this.dataSource);
      } else if (this.dataSource.startsWith('{') && this.dataSource.endsWith('}')) {
        this._dataSource = JSON.parse(this.dataSource) as FetchOption;
      } else {
        this._dataSource = { url: this.dataSource } as FetchOption;
      }
    } else {
      this._dataSource = this.dataSource;
    }
  }

  @Watch('nodeSelectableRule')
  watchNodeSelectableRuleHandler() {
    if (typeof this.nodeSelectableRule === 'string') {
      if (isModuleFunctionString(this.nodeSelectableRule)) {
        this._nodeSelectableRule = createFunctionFromString(this.nodeSelectableRule);
      }
    } else {
      this._nodeSelectableRule = this.nodeSelectableRule;
    }
  }

  @Watch('nodeCheckRule')
  watchNodeCheckRuleHandler() {
    if (typeof this.nodeCheckRule === 'string') {
      if (isModuleFunctionString(this.nodeCheckRule)) {
        this._nodeCheckRule = createFunctionFromString(this.nodeCheckRule);
      }
    } else {
      this._nodeCheckRule = this.nodeCheckRule;
    }
  }

  @Watch('dataSet')
  async watchDataSetHandler() {
    this.cleanNodes();
    await this.watchValueHandler();
    await this.watchCheckedHandler();
  }

  @Method()
  async getNodeById(id: string): Promise<object | null> {
    if (!this._items) return null;
    return this._items.find(x => (getFieldValue(x, this.fieldId) as string) === id);
  }

  @Method()
  async getTreeNode(item: object): Promise<HTMLStormTreeNodeElement> {
    return getFieldValue(item, this.fieldElement) as HTMLStormTreeNodeElement;
  }

  @Method()
  async getLabel(item: object): Promise<string> {
    return getFieldValue(item, this.fieldLabel) as string;
  }

  @Method()
  async unselectAll() {
    this.value = null;
  }

  @Method()
  async loadDataSet() {
    if (this._dataSet) return;
    if (this.dataSource || this.fetch) {
      try {
        const dataSet = await this.getDataSource();
        this.setDataSet(dataSet);
      } catch (error) {
        console.error('Error loading data:', error);
      }
    } else {
      this.setDataSet(this.dataSet);
    }
  }

  @Method()
  async getSelected(): Promise<object[]> {
    return this._items.filter(node => getFieldValue(node, this.fieldSelected) as boolean);
  }

  @Method()
  async getSelectedById(): Promise<string[]> {
    return await this.getSelected().then(x => x.map(y => getFieldValue(y, this.fieldId) as string));
  }

  @Method()
  async setSearchFocus() {
    if (this.searchable && this._searchRef) {
      await this._searchRef.setFocus();
    }
  }

  private readonly rerender = debounce(async (): Promise<void> => {
    if (!Array.isArray(this._items) || this._items.length === 0) return;
    await Promise.all(
      this._items.map(async item => {
        const node = await this.getTreeNode(item);
        if (node) {
          forceUpdate(node);
        }
      }),
    );
    forceUpdate(this);
  }, 100);

  renderNode(item: object): StormTreeNode {
    let content = getFieldValue(item, this.fieldLabel) as string;
    if (content && this._searchText) {
      content = appendHighlight(content, this._searchText);
    }

    return <storm-tree-node tree={this} dataSet={item} content={content} searchText={this._searchText}></storm-tree-node>;
  }

  async setNodeExpanded(item: object) {
    let parent = getFieldValue(item, this.fieldParent) as object;
    while (parent) {
      const node = await this.getTreeNode(parent);
      if (node) {
        await node.expand().catch(error => {
          console.error('Failed to expand tree node:', error);
        });
      } else {
        setFieldValue(parent, this.fieldExpanded, true);
      }

      parent = getFieldValue(parent, this.fieldParent) as object;
    }
  }

  cleanNodes() {
    this._dataSet = undefined;
    this._items = undefined;
  }

  async getDataSource(treeNode: StormTreeNode | null = null): Promise<any> {
    if (this.fetch) {
      return this.fetch(treeNode?.dataSet);
    } else {
      const options: FetchOption = typeof this._dataSource === 'function' ? this._dataSource(treeNode?.dataSet, this._searchText) : this._dataSource;
      return await fetchDataSource(options);
    }
  }

  async loadChild(treeNode: StormTreeNode) {
    try {
      const dataSource = await this.getDataSource(treeNode);
      const dataSet = this.dataSetParse(dataSource);

      await this.mergeChild(
        dataSet,
        this.fieldChildren.split('&').map(x => x.trim()),
      );
      setFieldValue(treeNode.dataSet, this.fieldChildLoaded, true);

      this._items = this.updateItems(this._dataSet);
    } catch (error) {
      console.error('Error loading data:', error);
    }
  }

  async mergeChild(source: any, fieldChildren: string[]): Promise<void> {
    for (const item of source) {
      const dest = await this.getNodeById(getFieldValue(item, this.fieldId) as string);

      for (const part of fieldChildren) {
        const sourcePartValue = getFieldValue(item, part) as object[];
        const destPartValue = getFieldValue(dest, part) as object[];

        if (!!sourcePartValue && (!destPartValue || destPartValue.length == 0)) {
          setFieldValue(dest, part, sourcePartValue);
        } else if (!!sourcePartValue && sourcePartValue.length > 0) {
          await this.mergeChild(sourcePartValue, [part]);
        }
      }
    }
  }

  dataSetParse(value: object | object[]): object[] {
    const clonedValue = JSON.parse(JSON.stringify(value));
    const structureType = this.structure === 'auto' ? (!Array.isArray(clonedValue) && !!this.fieldChildren ? 'tree' : 'array') : this.structure;

    switch (structureType) {
      case 'tree':
        return getFieldValue(clonedValue, this.fieldChildren) as object[];
      case 'array':
        return clonedValue as object[];
      default:
        throw new Error(`Unsupported structure type: ${structureType}`);
    }
  }

  setDataSet(value: object | object[]) {
    if (!value) return;

    this._dataSet = this.dataSetParse(value);
    this._items = this.updateItems(this._dataSet);

    this.rerender();
  }

  updateItems(value: object[], parent: object = null): object[] {
    if (!value || value.length === 0) return [];

    const items: object[] = [];
    for (const item of value) {
      let id = getFieldValue(item, this.fieldId) as string;
      if (id == null) {
        id = getFieldValue(item, this.fieldLabel) as string;
        setFieldValue(item, this.fieldId, id);
      }

      if (this.selection != 'none' && item[this.fieldSelectable] == null) {
        setFieldValue(item, this.fieldSelectable, calculateObjectValue(this, this._nodeSelectableRule, item) ?? true);
      }
      if (this.checkSelectable && item[this.fieldCheckSelectable] == null) {
        setFieldValue(item, this.fieldCheckSelectable, calculateObjectValue(this, this._nodeCheckRule, item) ?? true);
      }

      let level: number = 1;
      if (parent) {
        setFieldValue(item, this.fieldParent, parent);
        level = (getFieldValue(parent, this.fieldLevel) as number) + 1;
      }

      setFieldValue(item, this.fieldLevel, level);
      if (!(getFieldValue(item, this.fieldChildLoaded) as boolean)) {
        setFieldValue(item, this.fieldExpanded, this.expandLevel > level);
        setFieldValue(item, this.fieldChildLoaded, !this.partialLoad || this.expandLevel > level);
      }

      items.push(item);

      const children = getFieldValue(item, this.fieldChildren) as object[];
      if (children && children.length > 0) {
        items.push(...this.updateItems(children, item));
      }
    }

    return items;
  }

  async componentWillLoad() {
    await SharedStyles.applyShadowRootStylesAsync(this.host, 'material');
    this.expandIcon = this.expandIcon ?? (this.expandIconAlign === 'right' ? 'add' : 'chevron_right');
    this.collapseIcon = this.collapseIcon ?? (this.expandIconAlign === 'right' ? 'remove' : 'expand_more');
    this.watchDataSourceHandler();
    this.watchNodeSelectableRuleHandler();
    this.watchNodeCheckRuleHandler();
  }

  async componentWillRender() {
    if (this.loading === 'eager' || !this.partialLoad) {
      await this.loadDataSet();
    }

    if (this.selection != 'none' && !this.nullable && !this.value && this._items?.length) {
      this.value = getFieldValue(
        this._items.find(item => getFieldValue(item, this.fieldSelectable) as boolean),
        this.fieldId,
      ) as string;
    }

    await this.watchValueHandler();
    await this.watchCheckedHandler();
  }

  async componentDidLoad() {
    if (this._items?.length) {
      this.dateSetLoaded.emit();
    }
  }

  handleSearchChanged(ev: CustomEvent<string>) {
    ev.stopPropagation();
    const value = ev.detail?.trim() ?? '';
    if (this._searchText === value) return;
    if (value) {
      this.searchChanged.emit(value);
    }
  }

  renderSearch() {
    if (!this.searchable) return;
    return <FnSearchInput ref={elm => (this._searchRef = elm)} value={this._searchText} onValueChanged={ev => this.handleSearchChanged(ev)} />;
  }

  nodesRender() {
    let nodes: any[] = [];
    if (this.partialLoad || !this.searchable || this._searchText == null || !this._searchText.length) {
      nodes = this._dataSet;
    } else {
      nodes = this._items.filter(x => (getFieldValue(x, this.fieldLabel) as string).includes(this._searchText));
    }
    if (nodes.length === 0) return <div class="align-middle text-center p-4 h6">沒有找到符合的結果</div>;
    return nodes.map(item => this.renderNode(item));
  }

  render() {
    if (!this._dataSet) return;

    return (
      <Host>
        {this.renderSearch()}
        {this.nodesRender()}
      </Host>
    );
  }
}
